import React, { useCallback } from "react"
import { atom, useRecoilState } from "recoil"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"
import { WebData } from "store/tango"

import { flex } from "../../lib/flex"
import { webTheme } from "../../styles/webTheme"

interface DevSettingsState {
  game: NonNullable<WebData["game"]> | null
  sdkVersion: string | null
  cliVersion: string | null
  visible: boolean
  challengeNumber: number
}

const $devSettings = atom<DevSettingsState>({
  key: "$devSettings",
  default: {
    game: null,
    sdkVersion: null,
    cliVersion: null,
    visible: false,
    challengeNumber: 1,
  },
})

export function useDevSettings() {
  const [devSettings, setSettings] = useRecoilState($devSettings)
  const enabled = !!devSettings.game

  const setDevSettings = useCallback(
    (update: Partial<DevSettingsState>) => {
      setSettings((prev) => ({ ...prev, ...update }))
    },
    [setSettings]
  )

  const incrementDevChallengeNumber = useCallback(
    () =>
      setSettings((prev) => ({
        ...prev,
        challengeNumber: prev.challengeNumber + 1,
      })),
    [setSettings]
  )

  const decrementDevChallengeNumber = useCallback(
    () =>
      setSettings((prev) => ({
        ...prev,
        challengeNumber: prev.challengeNumber - 1,
      })),
    [setSettings]
  )

  return {
    devSettings,
    devSettingsEnabled: enabled,
    setDevSettings,
    incrementDevChallengeNumber,
    decrementDevChallengeNumber,
  }
}

export function DevSettings() {
  const {
    setDevSettings,
    incrementDevChallengeNumber,
    decrementDevChallengeNumber,
    devSettings: { visible, challengeNumber, sdkVersion, cliVersion },
  } = useDevSettings()

  const hide = useCallback(() => setDevSettings({ visible: false }), [
    setDevSettings,
  ])

  if (!visible) return null

  return (
    <Root onClick={hide}>
      <Modal onClick={(e) => e.stopPropagation()}>
        <Line>
          <Text>{skipTr(`Challenge number:`)}</Text>
          <Button onClick={incrementDevChallengeNumber}>{skipTr("+")}</Button>
          <Text>{challengeNumber}</Text>
          <Button onClick={decrementDevChallengeNumber}>{skipTr("-")}</Button>
        </Line>
        <Line>
          <Text>{skipTr(`SDK version: ${sdkVersion}`)}</Text>
        </Line>
        <Line>
          <Text>{skipTr(`CLI version: ${cliVersion}`)}</Text>
        </Line>
      </Modal>
    </Root>
  )
}

const Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  ${flex({ vAlign: "center", hAlign: "center" })};
  background-color: rgba(0, 0, 0, 0.7);
`

const Modal = styled.div`
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  > :not(:first-child) {
    margin-top: 5px;
  }
`

const Line = styled.div`
  ${flex({ horizontal: true, vAlign: "center" })};
  > :not(:first-child) {
    margin-left: 5px;
  }
`

const Text = styled.span`
  ${webTheme.text.body};
  color: #444;
`

const Button = styled.div`
  ${webTheme.text.body};
  color: white;
  padding: 2px 8px;
  background-color: #444;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
`
